<template>
	<div class="input-lang-group">
        <div class="input-group">
            <span class="input-group-text">UA</span>
            <input v-if="!isTextarea" type="text" v-model="content['ua']" class="form-control" @input="onChange()">
            <textarea v-if="isTextarea" type="text" v-model="content['ua']" class="form-control" @input="onChange()"></textarea>
            <span class="input-group-text">RU</span>
            <input v-if="!isTextarea" type="text" v-model="content['ru']" class="form-control" @input="onChange()">
            <textarea v-if="isTextarea" type="text" v-model="content['ru']" class="form-control" @input="onChange()"></textarea>
            <span class="input-group-text">EN</span>
            <input v-if="!isTextarea" type="text" v-model="content['en']" class="form-control" @input="onChange()">
            <textarea v-if="isTextarea" type="text" v-model="content['en']" class="form-control" @input="onChange()"></textarea>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            content: null
        }
    },
    props: ['value', 'isTextarea'],
    created() {
        this.content = this.value;
    },
    methods: {
        onChange() {
            this.$emit('onChange', this.content);
        }
    }
}
</script>
