<template>
	<div class="app-content" v-if="!!Object.keys(content).length">
		<div class="container">
			<div class="page-title">
				<h1>Корзина</h1>
				<div class="actions">
					<button class="btn btn-success" @click="savePage()" :disabled="saveInProgress">
						<span class="fas fa-save"></span>
						Сохранить
					</button>
				</div>
			</div>
			<div class="page-content">
				<div class="progress" v-if="saveInProgress">
					<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style="width: 100%"></div>
				</div>
				<div class="alert alert-success" v-if="isSavedSuccess" role="alert">
					Изменения успешно сохранены
				</div>
				<fieldset>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Заголовок страницы</label>
                            <InputLangGroup v-model="content.pageTitle" :content="content.pageTitle"/>
                        </div>
					</div>
				</fieldset>
                <fieldset>
                    <legend>Форма</legend>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Заголовок</label>
                            <InputLangGroup v-model="content.form.title" :content="content.form.title"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Поле "Имя"</label>
                            <InputLangGroup v-model="content.form.fields.name" :content="content.form.fields.name"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Поле "Email"</label>
                            <InputLangGroup v-model="content.form.fields.email" :content="content.form.fields.email"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Поле "Телефон"</label>
                            <InputLangGroup v-model="content.form.fields.phone" :content="content.form.fields.phone"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Поле "Адрес"</label>
                            <InputLangGroup v-model="content.form.fields.adress" :content="content.form.fields.adress"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Надпись "Итого"</label>
                            <InputLangGroup v-model="content.form.totalPriceText" :content="content.form.totalPriceText"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Кнопка оформления покупки</label>
                            <InputLangGroup v-model="content.form.btnText" :content="content.form.btnText"/>
                        </div>
					</div>
				</fieldset>
			</div>
		</div>
	</div>
</template>

<script>
import InputLangGroup from '../components/InputLangGroup.vue';

export default {
    data() {
        return {
            content: '',
            saveInProgress: false,
            isSavedSuccess: false
        }
    },
    components: {
        InputLangGroup
    },
    computed: {
        pageContent() {
            return this.$store.getters['getCartpageContent'];
        }
    },
    watch: {
        pageContent(value) {
            this.content = value;
        }
    },
    methods: {
        savePage() {
            this.saveInProgress = true;
            this.$store.dispatch('saveCartPage', this.content)
                .then(() => {
                    this.saveInProgress = false;
                    this.isSavedSuccess = true;
                    setTimeout(() => {
                        this.isSavedSuccess = false;
                    }, 3000);
                });
        }
    },
    created() {
        this.content = this.pageContent;
    },
    mounted() {

    }
};
</script>